.about{
    width: 100%;
    background-color: #76871D;
    color: white;
}
.about-wrapper{
    padding: 48px 16px;
    margin: 0 auto;
    width: 100%;
    display: grid;
    gap: 16px;
}

.about-content{
    line-height: 1.8;
    text-align: start;
}


@media screen and (min-width:768px) {
    .about-wrapper{
        width: 95%;
        margin: 0 auto;
    }
}

@media screen and (min-width:1024px) {
    .about-wrapper{
        width: 80%;
    }
}
@media screen and (min-width:1220px) {
    .about-wrapper{
        width: 1024px;
    }
}


